export default {
  data() {
    return {
      noteTypes: [
        {
          text: "",
          value: 1
        },
        {
          text: "",
          value: 2
        },
        {
          text: "",
          value: 3
        },
        {
          text: "",
          value: 4
        },
        {
          text: "",
          value: 5
        },
        {
          text: "",
          value: 94
        },
        {
          text: "",
          value: 95
        },
        {
          text: "LED",
          value: 96
        },
        {
          text: "",
          value: 97
        },
        {
          text: "BPM",
          value: 98
        },
        {
          text: "EOF",
          value: 99
        },
        {
          text: "",
          value: 100
        }
      ]
    };
  },
  methods: {
    noteOptions(note) {
      if ([3, 4].includes(note.type))
        return [
          {
            label: "width",
            type: "number",
            desc: "Float(1)"
          },
          {
            label: "offsetNumer",
            type: "number",
            desc: "Integer"
          },
          {
            label: "offsetDenom",
            type: "number",
            desc: "Integer"
          }
        ];
      else if (note.type === 94)
        return [
          {
            label: "source",
            type: "text",
            desc: "String(httphttps)"
          },
          {
            label: "width",
            type: "number",
            desc: "Float(1)"
          },
          {
            label: "height",
            type: "number",
            desc: "Float(1)"
          },
          {
            label: "offsetNumer",
            type: "number",
            desc: "Integer"
          },
          {
            label: "offsetDenom",
            type: "number",
            desc: "Integer"
          }
        ];
      else if (note.type === 95)
        return [
          {
            label: "length",
            type: "number",
            desc: "Integer(1)"
          }
        ];
      else if (note.type === 96)
        return [
          {
            label: "red",
            type: "number",
            desc: "Integer0-255"
          },
          {
            label: "green",
            type: "number",
            desc: "Integer0-255"
          },
          {
            label: "blue",
            type: "number",
            desc: "Integer0-255"
          }
        ];
      else if (note.type === 97)
        return [
          {
            label: "beat",
            type: "number",
            desc: "Integer[n/4]"
          }
        ];
      else if (note.type === 98)
        return [
          {
            label: "bpm",
            type: "number",
            desc: "FloatBPM"
          }
        ];
      else if (note.type === 100)
        return [
          {
            label: "comment",
            type: "text",
            desc: "String"
          }
        ];
      else return [];
    }
  }
};
